:root {
  --font-family: Vazirmatn, sans-serif;

  --color-blue-dark: #4A89DC;
  --color-blue-light: #5D9CEC;
  --color-aqua-dark: #3BAFDA;
  --color-aqua-light: #4FC1E9;
  --color-emerald-dark: #37BC9B;
  --color-emerald-light: #48CFAD;
  --color-green-dark: #8CC152;
  --color-green-light: #A0D468;
  --color-yellow-dark: #F6BB42;
  --color-yellow-light: #FFCE54;
  --color-orange-dark: #E9573F;
  --color-orange-light: #FC6E51;
  --color-red-dark: #DA4453;
  --color-red-light: #ED5565;
  --color-purple-dark: #967ADC;
  --color-purple-light: #AC92EC;
  --color-pink-dark: #D770AD;
  --color-pink-light: #EC87C0;

  --color-gray-darkest: #434A54;
  --color-gray-darker: #656D78;
  --color-gray-dark: #AAB2BD;
  --color-gray-light: #CCD1D9;
  --color-gray-lighter: #E6E9ED;
  --color-gray-lightest: #F5F7FA;

  --color-black: #000000;
  --color-white: #ffffff;

  --color-primary: var(--color-blue-dark);
  --color-primary-acent: var(--color-blue-light);
  --color-secondry: var(--color-yellow-dark);
  --color-secondry-acent: var(--color-yellow-light);
  --color-text: var(--color-gray-darkest);
  --color-background: var(--color-white);
  --color-border: var(--color-gray-light);

  --color-success: var(--color-green-dark);
  --color-info: var(--color-blue-dark);
  --color-danger: var(--color-red-dark);
  --color-warning: var(--color-yellow-dark);

  --header-font-size: 1.5rem;
  --header-line-height: 2.5;
  --header-size: calc(var(--header-font-size) * var(--header-line-height));

  --gap-size: 1rem;
  --container-width: 720px;
  --border-radius: 2rem;
}
